<template>
    <div class="wx-title">
        <mt-header :title="titles" fixed>
            <mt-button icon="back" @click="back" slot="left">返回</mt-button>
            <mt-button v-if="search" 
                        icon="search" 
                        @click="$router.push({name : 'search'})"
                        slot="right"></mt-button>
            <mt-button v-if="more" @click="showNav" icon="more" slot="right">
                <slot></slot>
            </mt-button>
            <mt-button v-if="add" @click="toParent" slot="right">{{add}}</mt-button>
        </mt-header>
        <div class="top_title" 
             @click="showMask">{{titles}}</div>
    </div>
</template>

<script>
    export default {
        props : {
            titles : {
                type : String
            },
            search : {
                type : String
            },
            more : {
                type : String
            },
            add : {
                type : String
            },
            showUnitTitle : {
                type : Boolean,
                default :false
            }
        },
        methods : {
            back() {
                this.$router.go(-1);
            },
            showNav() {
                this.$emit('navs');
            },
            toParent() {
                this.$emit('add');
            },
            showMask() {
                this.$emit('showMask');
            }
        }
    }
</script>

<style lang="scss" scope>
@include b(title) {
    position: fixed;
    width : 40px;
    height : 40px;
    z-index:10001;
    .top_title {
        position: fixed;
        top: 0;
        left: 50%;
        width:50%;
        margin:0 auto;
        padding : 0 10px;
        height : 40px;
        line-height : 40px;
        z-index:10001;
        font-size: 16px;
        color: #abd13e;
        text-align:center;
        transform : translateX(-50%);
    }
}
</style>



